/* Apply some general styles to the scrollable container */
body {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #00158f;
  color: white;
}

/* Style the custom scrollbar thumb */
.scrollable-content {
  width: 300px;
  height: 300px;
  overflow: auto;
}

/* Set the background image of the scrollbar thumb */
.scrollable-content::-webkit-scrollbar-thumb {
  width: 80px; /* Make the thumb wider */
  height: 50px;
  background-image: url(../Boutonscroll.png);
  background-size: contain; /* Scale the image to fit inside the thumb */
  background-repeat: no-repeat; /* Prevent the image from repeating */
}

/* Set the background color and border-radius of the scrollbar */
.scrollable-content::-webkit-scrollbar {
  width: 40px;
  border-radius: 20px; /* Adjust this value to control the border radius */
}

/* Hide the scrollbar track */
.scrollable-content::-webkit-scrollbar-track {
  background-color: transparent;
}

/* Hide the scrollbar buttons (arrows) */
.scrollable-content::-webkit-scrollbar-button {
  display: none;
}
.scroll_bar {
  width: 16px;
  height: 400px;
  background-color: #25336a;
  margin-left: -25px;
  z-index: -10;
  border-radius: 12px;
  margin-top: -12px;
}
.flexable {
  display: flex;
}
