body {
  color: #fff;
  background-color: #00158f;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

.scrollable-content {
  width: 300px;
  height: 300px;
  overflow: auto;
}

.scrollable-content::-webkit-scrollbar-thumb {
  background-image: url("Boutonscroll.f280b3dd.png");
  background-repeat: no-repeat;
  background-size: contain;
  width: 80px;
  height: 50px;
}

.scrollable-content::-webkit-scrollbar {
  border-radius: 20px;
  width: 40px;
}

.scrollable-content::-webkit-scrollbar-track {
  background-color: #0000;
}

.scrollable-content::-webkit-scrollbar-button {
  display: none;
}

.scroll_bar {
  z-index: -10;
  background-color: #25336a;
  border-radius: 12px;
  width: 16px;
  height: 400px;
  margin-top: -12px;
  margin-left: -25px;
}

.flexable {
  display: flex;
}

